<template>
    <div class="all">
        <!--  -->
        <div class="top">
            <router-link to="/load" style="text-decoration: none;">
            <span class="top1"></span>
            </router-link>
            <span class="top2"></span>
        </div>
        <!--  -->
        <div class="phone">
            <div class="phone1">
                <span>+86</span>
                <input type="text" placeholder="" class="phonenum" @click="phonenum">
                <span class="phone1-1" @click="phone11"></span>
            </div>
        </div>
        <!--  -->
        <div class="yan">
            <div class="yan1">
                <span class="yan1-1"></span>
                <span class="yan1-2" @click="reg"></span>
            </div>
        </div>
        <!--  -->
        <div class="cover">
            <div class="cover1">
                <!--  -->
                <p class="cover1-1"></p>
                <!--  -->
                <div class="cover1-2">
                    <p><span></span></p>
                    <p><a href="https://in.m.jd.com/help/app/register_info.html"></a></p>
                    <p><a href="https://in.m.jd.com/help/app/private_policy.html"></a></p>
                    <p><a href="https://in.m.jd.com/help/app/order_sharing_info.html"></a></p>
                    <p><span></span></p>
                </div>
                <!--  -->
                <div class="cover1-4">
                    <p class="cover1-4-1"><span><a href="https://in.m.jd.com/help/app/register_info.html"></a><a href="https://in.m.jd.com/help/app/private_policy.html"></a><a href="https://in.m.jd.com/help/app/order_sharing_info.html"></a></span></p>
                </div>
                <!--  -->
                <div class="cover1-3">
                    <router-link to="/load" style="text-decoration: none;">  
                        <span class="cover1-3-1"></span>
                    </router-link>
                    <span class="cover1-3-2" @click="argee"></span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// jquery
import $ from 'jquery'

export default {
    // 
    methods:{
        phone11(){
            console.log($(event.target))
            $('.phone1-1').css('display','none')
            $(event.target).parent().children()[1].value ="";
        },
        phonenum(){
            $('.phone1-1').css('display','block')
        },
        argee(){
            $('.cover').css('display','none')
        },
        reg(){
            var mobile = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
            if(mobile.test($('.phonenum').val())){
                $('.yan1-2')[0].innerHTML='';
            }else{
                alert('')
            }
        }
    }
};
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
}
.all {
  box-sizing: border-box;
  width: 100%;
  height: 736px;
  overflow: hidden;
  background-color: #f7f7f7;
}
.top {
  width: 100%;
  height: 45px;
  border-bottom: 1px solid #bbb;
  background: #f9f9f9;
  color: #616161;
}
.top1 {
  float: left;
  background: url(/static/pic/load/iconBg.png) center -62px no-repeat;
  background-size: 100% auto;
  width: 22px;
  height: 22px;
  margin: 10px;
}
.top2 {
  color: #616161;
  float: left;
  margin-left: 30%;
  margin-top: 10px;
}
.phone1 {
  width: 90%;
  background: white;
  padding: 10px 20px;
  box-sizing: border-box;
  margin: 30px auto;
}
.phone1 input {
  border: none;
  outline: none;
}
.phone1-1{
    float: right;
    width: 22px;
    height: 22px;
    background: url(/static/pic/load/iconBg.png) center 0px no-repeat;
    background-size: 100% auto;
    display: none;
}
.yan {
  width: 90%;
  background: white;
  padding: 10px 20px;
  box-sizing: border-box;
  margin: 30px auto;
  border: 1px solid #409eff;
}
.yan1-1 {
  display: inline-block;
  background: url(/static/pic/load/init.gif) no-repeat;
  width: 22px;
  height: 22px;
  background-size: 100% auto;
}
.yan1-2 {
  margin: 0 10px;
  color: #409eff;
  font-size: 14px;
}
.cover {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.5);
  display: block;
}
.cover1 {
  width: 100%;
  background: #f8f8f8;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.cover1-1{
    text-align: center;
    line-height: 50px;
    color: #000;
    font-size: 18px;
    font-weight: bold;
    border-bottom: 1px solid #999;
}
.cover1-3-1 {
  display: inline-block;
  width: 40%;
  border: 1px solid #d7d7d7;
  text-align: center;
  line-height: 40px;
  background: white;
  margin: 10px;
}
.cover1-3-2 {
  float: right;
  width: 40%;
  border: 1px solid #d7d7d7;
  text-align: center;
  line-height: 40px;
  background: red;
  margin: 10px;
  color: white;
}
.cover1-4 a{
    color: #E93B3D;
}
.cover1-2{
    height: 190px;
    overflow-x: hidden;
    overflow-y: scroll;
    font-size: 15px;
}
.cover1-2 span{
    color: black;
    font-weight: bold;
    text-decoration: underline;
}
</style>



